.Sidebar {
  height: 100vh;
  flex: 0.2;
  color: white;
  padding-left: 10px;
  padding-right: 10px;
  min-width: 230px;
  background-color: black;
}
.Sidebar_logo {
  margin-right: auto;
  height: 60px;
  padding: 15px;
}
.sidebar > hr {
  border: 1px solid #282828;
  width: 90%;
  margin: 10px auto;
}
.sidebar_title {
  margin-left: 10px;
  padding: 5px;
  font-size: 12px;
}
